import CategoryBar from './category-bar';
import { Link } from 'react-router-dom';
import './styles/navbar.css';
// import logo from './images/лого.PNG';

function NavBar() {
  return (
    <div className="NavBar">
      <img className="LogoImg" src={require('./images/лого.PNG')} alt='Логотип' />

      <div className="RightNav">
        <div className="UpNav">
          <Link className="NavBarElement" to={{ pathname: `/`, fromDashBoard: false }}>Главная</Link>
          <div className="NavBarElement">
            <Link className="NavBarElement OpenHidden" to={{ pathname: `/categories`, fromDashBoard: false }}>Категории</Link>
            <div className="HiddenBar">
                <CategoryBar />
            </div>
          </div>
        </div>
  
        <div className="BottomNav">
        <Link className="NavBarElement" to={{ pathname: `/profile`, fromDashBoard: false }}>
          {/* <img className="Icon" src={require('./images/лого.PNG')} alt='Логотип' /> */}
        </Link>
        </div>
      </div>
      <Link className="NavBarElement" to={{ pathname: `/about`, fromDashBoard: false }}>О проекте</Link>
    </div>
  );
}

export default NavBar;
